import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Addresses')
  },
  details(parameter) {
    return Api().get(`Addresses/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Addresses', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Addresses/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter, headers) {
    return Api().delete(`Addresses/${parameter}`, {
      headers,
    })
  },
}



