<template>
  <div class="price-tag">
    <v-icon color="green darken-1" class="for-icon">style</v-icon>
    <span class="value">
      {{formatedPrice}}
    </span>
    <span class="cfa">f cfa</span>
  </div>
</template>
<script>
export default {
  name: 'price',
  props: {
    value: {
      type: Number,
      required: true,
    },
  },
  computed: {
    formatedPrice() {
      return this.value.toLocaleString().replace(/,/g, '.')
    },
  },
}
</script>

<style lang="sass">
  .price-tag
    float: left
    vertical-align: middle
    border-radius: 3px
    font-size: 1.1rem
    border: 1px solid #1db14b
    .for-icon
      font-size: 1.2rem!important
      padding-left: 5px
    span
      font-family: robotoBold!important
    .cfa
        padding: 3px 5px
        background-color: #1db14b
        color: #fff
    .value
        padding: 7px 10px 7px 2px
</style>



