<template>
  <div class="adresses">
    <v-toolbar
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <back></back>
      <v-toolbar-title>Adresse de livr.</v-toolbar-title>
    </v-toolbar>
    <Loading v-if="loading"></Loading>

    <v-container
      fluid
      transition="slide-y-reverse-transition"
    >
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4 v-if="!loading">
          <h6 class="address-select">Veuillez sélectionner une adresse pour la livraison</h6>
          <v-radio-group v-model="address" :mandatory="false">
            <v-radio
              v-for="addr in addresses" :key="addr.id"
              :label="addr.country"
              :value="addr.id"
              class="mt10 mb10">
            </v-radio>
          </v-radio-group>
            <v-btn
              block
              color="blue darken-1"
              class="mb20 to-dark"
              :disabled="!address"
              :to='{name: "Payment"}'
            >
              Continuez
            </v-btn>
          <a class="help-link" @click="addressAdd = true">
            Ou créez une nouvelle adresse en cliquant ici !
          </a>
        </v-flex>
      </v-layout>

      <v-dialog
        v-model="addressAdd"
        fullscreen
        transition="dialog-bottom-transition"
        :overlay="false"
        scrollable
      >
        <v-card>
          <v-toolbar dark class="oq-toolbar">
            <v-btn icon @click.native="addressAdd = false">
              <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>Nouvelle adresse</v-toolbar-title>
          </v-toolbar>

          <v-card-text>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-text-field
                class="input"
                label="Nom et prénoms du destinataire"
                v-model="newaddr.fullname"
                :rules="nameRule"
                required
              ></v-text-field>
              <v-text-field
                class="input"
                :rules="phoneRule"
                label="Téléphone"
                v-model="newaddr.phone"
                required
              ></v-text-field>
              <v-select
                label=" Choisissez le Pays de destination"
                v-model="newaddr.country"
                :items="countries"
                :rules="[v => !!v || 'Le choix du pays est obligatoire']"
                required
              ></v-select>
              <v-select
                label="Choisissez la ville"
                v-model="newaddr.city"
                :items="cities"
                :rules="[v => !!v || 'Le choix de la ville est obligatoire']"
                required
              ></v-select>
              <v-text-field
                class="input"
                box
                multi-line
                :rules="indicRule"
                label="Indiquez votre Adresse SVP."
                v-model="newaddr.indication"
                required
              ></v-text-field>
              <v-btn
                color="blue darken-1"
                style="color: white"
                @click="addAddress()"
                :disabled="isButtonDisabled"
                class="val-btn"
              >
                Ajouter l' addresse
              </v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-container>
  </div>
</template>

<script>
import _ from 'lodash'
import AddressService from '@/services/AddressService'
import back from '@/components/includes/Back'
import Loading from '@/components/includes/Loading'

export default {
  name: 'addresses',
  components: {
    back,
    Loading,
  },
  data() {
    return {
      // addresses: 'address-1',
      addressAdd: false,
      loading: false,
      valid: false,
      addresses: [],
      address: '',
      countries: ['Benin'],
      cities: ['Cotonou', 'Abomey-Calavi'],
      newaddr: {
        fullname: '',
        country: '',
        city: '',
        indication: '',
        phone: '',
        lat: 10,
        lng: 10,
      },
      nameRule: [
        // eslint-disable-next-line
        v => !!v||'Le nom est obligatoire',
      ],
      phoneRule: [
        // eslint-disable-next-line
        v => !!v||'Le numéro est invalide',
        // eslint-disable-next-line
        v => /(?:\s+)?229(97|96|67|66|61|62|99|98|95|94|68|65|64|60)(\d\d)(\d\d)(\d\d)(?:\s+)?$/.test(v)||'Le numéro de téléphone n\'est pas valide ( exemple: 22966xxxxxx) ',
      ],
      indicRule: [
        // eslint-disable-next-line
        v => !!v||'L\'Indication est obligatoire',
      ],
    }
  },
  created() {
    this.initialData()
  },
  async mounted() {
    this.initialData()
  },
  methods: {
    async initialData() {
      this.loading = true
      const addr = await AddressService.list()
      try {
        this.addresses = addr.data
        this.addresses = _.filter(addr.data, { user: this.$store.state.user })
        this.loading = false
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
    async addAddress() {
      this.loading = true
      try {
        const num = this.newaddr.phone.trim()
        const data = {
          fullname: this.newaddr.fullname,
          country: this.newaddr.country,
          city: this.newaddr.city,
          indication: this.newaddr.indication,
          phone: num,
          user: this.$store.state.user,
          location: {
            lat: this.newaddr.lat,
            lng: this.newaddr.lng,
          },
        }
        const h = {
          Authorization: `${this.$store.state.token}`,
        }
        const a = await AddressService.add(data, h)
        this.addresses.push(a.data)
        this.loading = false
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
  },
  computed: {
    isButtonDisabled() {
      if (this.valid
      && (this.newaddr.fullname
        && this.newaddr.city
        && this.newaddr.country
        && this.newaddr.indication
        && this.newaddr.phone)
      ) {
        return false
      }
      return true
    },
  },
}
</script>


<style lang="sass">
.myform
  input, select
    height: 40px
    line-height: 40px
    border: 1px solid #ccc
    width: 100%
    padding: 0 5px
    border-radius: 3px
    margin-bottom: 20px
    margin-top: 10px
  
  .btn--disabled.to-dark
    color: #aaa!important
    background-color: #E0E0E0!important
</style>



