import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import _ from 'lodash'

Vue.use(Vuex)

/* eslint-disable */

const store = new Vuex.Store({
  strict: false,
  state: {
    token: '',
    user: '',
    isUserLoggedIn: false,
    cart: [],
    order: '',
  },
  plugins: [
    createPersistedState()
  ],
  getters: {
    // Cette foncion récupère le nombre total d'article
    // dans le Panier
    cartTotalItems: state =>{
      return state.cart.length
    },
  },
  mutations: {
    // Edit le token du store avec les infos
    // de l'utilisateur connecté
    setToken: (state, token) => {
      state.token = token
      if (token) {
        state.isUserLoggedIn = true
      } else {
        state.isUserLoggedIn = false
      }
    },
    // Passe les information globale de 
    // l'utilisateur connecté au user du store
    setUser: (state, user) => {
      state.user = user
    },
    // Ajoute un article au panier
    setCart: (state, row) => {
      const cartElementsId = []
      for (let i = 0; i < state.cart.length; i++) {
        const element = state.cart[i]
        cartElementsId.push(element.item.id)
      }
      if (!(cartElementsId.includes(row.item.id))) {
        state.cart.push(row)
      }
      const e = _.find(state.cart, { 'item': row.item })
      e.quantity = row.quantity
    },
    // Cette fonction permet de retirer un article du Panier
    removeFromCart: (state, item) => {
      let len = state.cart.length
      let index = state.cart.indexOf(item)
      if (index !== -1) {
        state.cart.splice(index, 1)
      }
      let i = len - 1
      state.cart.splice(i, 1)
    },
    // Fonction permettant de vider le Panier 
    cleanCart: (state) => {
      state.cart = []
    },
    // Récupère l'Id de la commande
    setOrder: (state, order) => {
      state.order = order
    },
  },
  actions: {
    // A chacune des Actions correspond une et une seule mutation
    setTokenAct: ({ commit }, token) => {
      commit('setToken', token)
    },
    setUserAct: ({ commit }, user) => {
      commit('setUser', user)
    },
    setCartAct: ({ commit }, item) => {
      commit('setCart', item)
    },
    removeFromCartAct: ({ commit }, item) => {
      commit('removeFromCart', item)
    },
    cleanCartAct: ({ commit }) => {
      commit('cleanCart')
    },
    setOrderAct: ({ commit }, order) => {
      commit('setOrder', order)
    },
  },
})

export default store



