import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Caracteristics')
  },
  details(parameter) {
    return Api().get(`Caracteristics/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Caracteristics', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Caracteristics/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter, headers) {
    return Api().delete(`Caracteristics/${parameter}`, {
      headers,
    })
  },
}



