import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Payments')
  },
  details(parameter) {
    return Api().get(`Payments/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Payments', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Payments/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter, headers) {
    return Api().delete(`Payments/${parameter}`, {
      headers,
    })
  },
}



