import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Stocks')
  },
  details(parameter) {
    return Api().get(`Stocks/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Stocks', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Stocks/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter, headers) {
    return Api().delete(`Stocks/${parameter}`, {
      headers,
    })
  },
}



