import Api from '@/services/Api'

export default {
  register(credentials) {
    return Api().post('Users', credentials)
  },
  login(credentials) {
    return Api().post('Users/login', credentials)
  },
  logout(credentials) {
    return Api().post('Users/logout', credentials)
  },
  details(parameter, headers) {
    return Api().post(`Users/${parameter}`, {
      headers,
    })
  },
}



