<template>
  <div class="pa0 ma0">
    <v-toolbar-side-icon @click="goBack()">
      <v-icon class="ml0">arrow_back</v-icon>
    </v-toolbar-side-icon>
  </div>
</template>
<script>
export default {
  name: 'back',
  methods: {
    goBack() {
      this.$router.go(-1)
    },
  },
}
</script>




