<template>
  <div class="home-content">
    <Loading v-if="loading"></Loading> 
    <v-container
      fluid
      transition="slide-y-reverse-transition"
      class="product-list"
    >
      <v-layout row wrap>
        <v-flex xs12 sm6 md3 class="item mt-1 pr-2" v-for="article in articles" :key="article.id">
          <v-layout row wrap>
            <v-flex xs4 class="pr-1 picture">
              <router-link :to="{ name: 'Detail', params:{id: `${article.id}` } }">
                <img :src="download(article.image)" alt="#" class="totalWidthImg">
              </router-link>
            </v-flex>
            <v-flex xs8 class="pl-1">
              <router-link :to="{ name: 'Detail', params:{id: `${article.id}` } }">
                <h6 class="label"> {{article.label}} </h6>
              </router-link>
              <p class="description mt-1 mb-1"> {{article.description | truncate(100)}} </p>
              <div class="price">
                <Price :value="article.finalprice" ></Price>
              </div>
            </v-flex>
          </v-layout>
          <hr class="divide">
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import ArticleService from '@/services/ArticleService'
import Loading from '@/components/includes/Loading'
import Price from '@/components/includes/Price'

export default {
  components: {
    Loading,
    Price,
  },
  data() {
    return {
      articles: [],
      loading: false,
    }
  },
  async mounted() {
    this.loading = true
    const allarticles = await ArticleService.list()
    try {
      this.articles = allarticles.data
      this.loading = false
    } catch (error) {
      // eslint-disable-next-line
      console.log(error)   
    }
  },
  methods: {
    download(file) {
      if (process.env.NODE_ENV === 'production') {
        return `https://api.tecnonia.com/api/attachements/product_pictures/download/${file}`
      }
      return `https://api.tecnonia.com/api/attachements/product_pictures/download/${file}`
    },
  },
}
</script>



