<template>
  <div>
      <!-- :mini-variant="miniVariant" -->
      <!-- :clipped="clipped" -->
    <v-navigation-drawer
      persistent
      v-model="drawer"
      enable-resize-watcher
      fixed
      app
      temporary
      class="menu"
      style="z-index: 70000!important"
    >
      <div class="pa10 user-info">
        <v-avatar size="60px" class="white">
          <v-icon x-large color="red" dark class="profil block">account_circle</v-icon>
        </v-avatar>
        <!-- <div class="mt20 mb10 email"> mon@email.com </div> -->
        <a href="#" v-show="isLoggedIn()" @click="Logout()" class="sign">Déconnexion</a>
        <router-link v-show="!isLoggedIn()" :to="{ name: 'Login'}" class="rounded">Connexion</router-link>
      </div>

      <div class="links">
        <li>
          <router-link :to="{ name: 'Home'}" class="menu-item">
            <v-icon color="grey darken-1" class="mr20">home</v-icon> <span @click="drawer=false">Accueil</span>
          </router-link>
        </li>

        <li>
          <router-link :to="{ name: 'Panier'}" class="menu-item">
            <v-icon color="grey darken-1" class="mr20">shopping_cart</v-icon> <span>Panier</span>
          </router-link>
        </li>

        <li>
          <a @click="shareApp">
            <v-icon color="grey darken-1" class="mr20">share</v-icon>
            <span>Partager l'Application</span>
          </a>
        </li>
      </div>
    </v-navigation-drawer>
    <v-toolbar
      :clipped-left="clipped"
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>
      <div>
        <router-link :to="{ name: 'Panier'}" >
          <v-btn icon>
            <v-icon>shopping_cart</v-icon>
          </v-btn>
        </router-link>
        <!-- <router-link :to="{ name: 'Search'}" >
          <v-btn icon>
            <v-icon>search</v-icon>
          </v-btn>
        </router-link> -->
      </div>
    </v-toolbar>
  </div>
</template>

<script>
// import LoginService from '@/services/LoginService'

export default {
  data() {
    return {
      clipped: false,
      drawer: false,
      title: 'Oqenyite',
    };
  },
  name: 'App',
  methods: {
    Logout() {
      // LoginService.logout()
      this.$store.dispatch('setTokenAct', '')
      this.$store.dispatch('setUserAct', '')
      this.$store.dispatch('cleanCartAct')
      this.$router.push({ name: 'Login' })
    },
    isLoggedIn() {
      return this.$store.state.isUserLoggedIn
    },
    shareApp() {
      if (typeof window.plugins === 'undefined') {
        // eslint-disable-next-line
        console.log('Can\'t share on mobile web');
      } else {
        const options = {
          message: 'Téléchargez l\'application android de la Panafricaine de l\'E-commerce', // not supported on some apps (Facebook, Instagram)
          subject: 'Oqenyite', // fi. for email
          // files: ['', ''], // an array of filenames either locally or remotely
          url: 'https://play.google.com/store/apps/details?id=tecnonia.com.oqenyite&hl=fr',
          chooserTitle: 'Choisir une Application', // Android only, you can override the default share sheet title
        };
        window.plugins.socialsharing.shareWithOptions(options, (result) => {
          // eslint-disable-next-line
          console.log('Sharing success');
          // eslint-disable-next-line
          console.log(result);
          // this.$ons.notification.alert('Share post success');
          // navigator.app.exitApp();
          this.$router.back();
        }, (error) => {
          // eslint-disable-next-line
          console.log('Sharing error');
          // eslint-disable-next-line
          console.log(error);
          // this.$ons.notification.alert('Share post failed :(');
        });
      }
    },
  },
};
</script>


