<template>
  <div class="payments">
    <v-toolbar
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <back></back>
      <v-toolbar-title>Paiement</v-toolbar-title>
    </v-toolbar>

    <v-container
      fluid
      transition="slide-y-reverse-transition"
    >
      <v-layout row wrap>
        <!-- <v-flex xs12 sm6 md4 offset-sm3 offset-md4>
          <h6 class="payment-title">Détails de la commande</h6>
          <table>
            <tr>
              <td>Total du Panier</td>
              <td>2 543 000 f cfa</td>
            </tr>
            <tr>
              <td>Frais de livraison</td>
              <td>13 500 f cfa </td>
            </tr>
            <tr>
              <td> <b>Total</b> </td>
              <td> <b>2 556 500 f cfa</b> </td>
            </tr>
          </table>
        </v-flex> -->

        <v-flex xs12 sm6 md4 offset-sm3 offset-md4>
          <h6 class="payment-title">Sélectionner un moyen de paiement</h6>
          <v-radio-group v-model="payment.mean" :mandatory="false">
            <v-radio label="Moov Money (Flooz)" value="Moov Money" class="mb10"></v-radio>
            <v-radio label="MTN Mobile Money (MoMo)" disabled value="MTN Mobile Money" class="mb10"></v-radio>
            <v-radio disabled label="Carte Visa" value="Carte Visa" class="mb10"></v-radio>
          </v-radio-group>
            <v-btn
              color="blue darken-1"
              :disabled="!payment.mean"
              block
              @click.native="paymentAccount = true"
              class="mb20 mt0"
            >
              Payer
            </v-btn>
        </v-flex>
      </v-layout>

      <!-- PAYMENT ACCOUNT DIALOG BOX -->
      <!-- ========================== -->

      <v-dialog v-model="paymentAccount" persistent max-width="250">
        <v-card>
          <v-card-title>
            <h6 class="dialog-title">Précisez votre numéro de compte</h6>
          </v-card-title>
          <v-card-text>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-text-field
                class="input"
                :rules="mtnNumberRule"
                label="Numéro de compte"
                v-model="payment.phone"
                v-if="payment.mean==='MTN Mobile Money'"
                required
              ></v-text-field>
              <v-text-field
                class="input"
                :rules="moovNumberRule"
                label="Numéro de compte"
                v-if="payment.mean==='Moov Money'"
                v-model="payment.phone"
                required
              ></v-text-field>
              <!-- <input type="text" class="numero" v-model="payment.phone"> -->
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat @click.native="clean()">Annuler</v-btn>
            <v-btn
              color="blue darken-1"
              flat
              :disabled='isButtonDisabled'
              @click="pay()"
              @click.native="paymentAccount = false"
            >
              Payer
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'
// import AddressService from '@/services/AddressService'
import OrderService from '@/services/OrderService'
import PaymentService from '@/services/PaymentService'
import back from '@/components/includes/Back'

const FCM = require('fcm-push');

export default {
  name: 'payment',
  components: {
    back,
  },
  data() {
    return {
      paymentMode: '',
      paymentAccount: false,
      valid: true,
      errs: {},
      payment: {
        mean: '',
        phone: '',
        order: '',
        amount: 0,
      },
      mtnNumberRule: [
        // eslint-disable-next-line
        v => !!v||'Le numéro est invalide',
        // eslint-disable-next-line
        v => this.payment.mean==='MTN Mobile Money' && /(?:\s+)?229(97|96|67|66|61|62)(\d\d)(\d\d)(\d\d)(?:\s+)?$/.test(v)||'Ceci n\'est pas un numéro MTN (exemple: 22997XXXXXX)',
      ],
      moovNumberRule: [
        // eslint-disable-next-line
        v => !!v||'Le numéro est invalide',
        // eslint-disable-next-line
        v => this.payment.mean==='Moov Money' && /(?:\s+)?229(99|98|95|94|68|65|64|60)(\d\d)(\d\d)(\d\d)(?:\s+)?$/.test(v)||'Ceci n\'est pas un numéro Moov (exemple: 22995XXXXXX)',
      ],
    }
  },
  methods: {
    async pay() {
      try {
        const order = await OrderService.details(this.$store.state.order)
        this.payment.amount = order.data.total_amount
        const h = {
          Authorization: `${this.$store.state.token}`,
        }
        const dat = new Date()
        const t = dat.toLocaleTimeString().trim()
        const d = dat.toLocaleDateString().trim()
        const ref = d + t
        const num = this.payment.phone.trim()
        const data = {
          order: ref,
          mode: this.payment.mean,
          amount: this.payment.amount,
          number: num,
        }
        const p = await PaymentService.add(data, h)
        if (this.payment.mean === 'MTN Mobile Money') {
          this.momopay(p.data.amount, p.data.number, ref)
        } else {
          this.floozPay(p.data.amount)
        }
        this.paymentNotif(p.data.amount)
        this.loading = false
        this.cleancart()
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
    cleancart() {
      this.$store.dispatch('cleanCartAct')
    },
    floozPay(a) {
      // eslint-disable-next-line
      const nbr = `*155*1*1*1*94533232*94533232*${a}#`
      if (typeof window.plugins.CallNumber === 'undefined') {
        // eslint-disable-next-line
        alert('Ce Appareil ne supporte pas la fonctionnalité d\'appel')
      } else {
        // eslint-disable-next-line
        const onSuccess = (e) => { console.log(e) }
        // eslint-disable-next-line
        const onError = (e) => { console.log(e) }
        window.plugins.CallNumber.callNumber(onSuccess, onError, nbr, true);
      }
    },
    paymentNotif(am) {
      const storage = window.localStorage;
      const deviceID = storage.getItem('user_token');
      // eslint-disable-next-line
      const msg = 'Le payement de la somme de '+am+' est en cours de validation'
      const apiKey = 'AAAAYOqV0sw:APA91bFpZjx6U7g4x60renFoI6sJ-RcIu83JI5PUgGXwAXC6Va9goZxT87ZP95rCgHTkyTGu0gxnnzHw2gzuBE4g0IIHM6ApOuHyNWgQDBKnDWah5tRMiC5r7cT4XiPxx4uP86SLZcGA';
      const fcm = new FCM(apiKey);
      const message = {
        to: deviceID,
        collapse_key: 'tecnonia.com.oqenyite',
        notification: {
          title: 'Payement en cours de validation',
          body: msg,
          color: '#d00000',
        },
      };

      fcm.send(message, (err, response) => {
        if (err) {
          // eslint-disable-next-line
          alert(err);
          // eslint-disable-next-line
          console.log("Something has gone wrong!");
        } else {
          // eslint-disable-next-line
          console.log("Successfully sent with response: "+ response);
        }
      });
    },
    async momopay(cost, phone, ref) {
      try {
        const data = {
          msisdn: phone,
          amount: cost,
          transref: ref,
          clientid: 'OQEDEV',
        }
        await axios({
          method: 'post',
          url: 'http://74.208.84.251:8221/QosicBridge/user/requestpayment',
          data,
          auth: {
            username: 'USR08',
            password: 'YG739G5XFVPYYV4ADJVW',
          },
        })
      } catch (error) {
        this.errs = error
      }
    },
    clean() {
      this.payment.mean = ''
      this.payment.phone = ''
      this.paymentAccount = false
    },
  },
  computed: {
    isButtonDisabled() {
      if (this.valid && this.payment.phone) {
        return false
      }
      return true
    },
  },
};
</script>

<style lang="sass">
  .dialog-title
    font-size: 1.2rem
    font-family: robotoBold!important
    color: #555

  .numero
    height: 35px
    line-height: 35px
    border: 1px solid #ccc
    border-radius: 3px
    padding-left: 5px
    width: 100%
    outline: none
  
  button.blue
    color: #fff!important
</style>


