<template>
  <div>
      <!-- :clipped-left="clipped" -->
    <v-toolbar
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <v-toolbar-title>Inscription</v-toolbar-title>
    </v-toolbar>

    <v-container
      fluid
      transition="slide-y-reverse-transition"
      class="connexion"
    >
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-text-field
              label="Votre nom d'utilisateur"
              v-model="username"
              :rules="usernameRules"
              required
            ></v-text-field>
            <v-text-field
              label="Votre email"
              v-model="email"
              :rules="emailRules"
              required
            ></v-text-field>
            <v-text-field
              type="Password"
              label="Votre mot de passe"
              v-model="password"
              :rules="passwordRules"
              required
            ></v-text-field>
            <v-text-field
              type="Password"
              label="Votre mot de passe"
              v-model="repassword"
              :rules="repasswordRules"
              required
            ></v-text-field>
          </v-form>
          <v-btn
            color="blue darken-1"
            style="color: white"
            @click="register()"
            :disabled="!valid"
            class="val-btn"
          >
            S'Inscrire
          </v-btn>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="inscription-text">
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4 class="text-xs-center">
          <div class="question"><span>Avez vous déjà un compte?</span></div>
          <div class="lien"><router-link :to="{ name: 'Login'}" >Se connecter</router-link></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import _ from 'lodash'
import AddressService from '@/services/AddressService'
import LoginService from '@/services/LoginService'

export default {
  name: 'signup',
  data() {
    return {
      email: '',
      username: '',
      password: '',
      repassword: '',
      loading: false,
      valid: false,
      emailRules: [
        // eslint-disable-next-line
        v => !!v||'Ce champ est obligatoire',
        // eslint-disable-next-line
        v => /\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3}(?:\s+)?)+$/.test(v)||'L\'e-mail doit être valide',
      ],
      passwordRules: [
        // eslint-disable-next-line
        v => !!v || 'Le mot de passe est obligatoire',
      ],
      repasswordRules: [
        // eslint-disable-next-line
        v => !!v || 'Entrez le mot de passe à nouveau',
        // eslint-disable-next-line
        v => this.password === this.repassword||'Les deux mots de passe ne sont pas identiques',
      ],
      usernameRules: [
        // eslint-disable-next-line
        v => !!v || 'Entrez votre nom d\'utiliateur',
        // eslint-disable-next-line
        v => v.length >= 6  || 'Le nom d\'utilisateur requiert au moins 6 caractères' ,
      ],
    }
  },
  created() {
    this.register()
  },
  methods: {
    async register() {
      this.loading = true
      const mail = this.email.trim()
      try {
        const registerdata = {
          username: this.username,
          email: mail,
          password: this.password,
        }
        const response = await LoginService.register(registerdata)
        this.loading = false
        const t = response.data.id
        const u = response.data.userId
        this.$store.dispatch('setTokenAct', t)
        this.$store.dispatch('setUserAct', u)
        this.redirect()
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
    isLoggedIn() {
      return this.$store.state.isUserLoggedIn
    },
    async redirect() {
      const addresses = await AddressService.list()
      try {
        const useraddresses = _.filter(addresses.data, { user: this.$store.state.user })
        if (useraddresses.length > 0) {
          this.$router.push({ name: 'Home' })
        } else {
          this.$router.push({ name: 'AdresseNew' })
        }
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
  },
}
</script>



