import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Articles')
  },
  details(parameter) {
    return Api().get(`Articles/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Articles', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Articles/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter) {
    return Api().delete(`Articles/${parameter}`)
  },
}



