<template>
  <div class="panier">
      <!-- :clipped-left="clipped" -->
    <v-toolbar
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <back></back>
      <v-toolbar-title>Panier</v-toolbar-title>
    </v-toolbar>
    <Loading v-if="loading"></Loading>

    <v-container
      fluid
      transition="slide-y-reverse-transition"
      class="pt0"
    >

      <!-- CART ITEMS LIST -->

      <v-layout row wrap class="items">
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4 v-if="!loading">
          <h6 class="section-title">Eléments du panier</h6>
          <hr class="hz">

          <div class="product-list cart">
            <v-layout row wrap class="item"
              v-if="AllInCart != []"
              v-for="row in AllInCart" :key="row.id"
            >
              <v-flex xs4 class="pr-1 picture">
                <router-link :to="{ name: 'Detail', params:{id: `${row.item.id}` }}">
                  <img :src="download(row.item.image)" alt="#" class="totalWidthImg">
                </router-link>
              </v-flex>
              <v-flex xs8 class="pl-1">
                <router-link :to="{ name: 'Detail', params:{id: `${row.item.article}` }}">
                  <h6 class="label">{{label(row.item.article)}}</h6>
                </router-link>
                <div class="price">
                  <!-- <span> {{price(row.item.article)}}</span> &nbsp; <span>f cfa</span> -->
                  <Price class="num" :value="price(row.item.article)" ></Price>
                </div> <br>
                <div class="qte">
                  <label for="item-qte">Quantité : </label>
                  <span> {{row.quantity}} </span> &nbsp;
                  <button @click="removeItem(row)">
                    <v-icon color="red" size="20px">remove_circle</v-icon>
                  </button> &nbsp;
                  <!-- <button @click="addOrRemove(1)">
                    <v-icon color="green" size="20px">add_circle</v-icon>
                  </button> -->
                </div>
              </v-flex>
            </v-layout>
          </div>
        </v-flex>
      </v-layout>

      <!-- APPLY PROMO CODE -->

      <!-- <v-layout class="promo-code" v-if="AllInCart.length">
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4>
          <h6 class="section-title">Appliquer un code promo</h6>
          <hr class="hz">

          <div class="form">
            <input type="text">
            <v-btn color="green darken-2" dark>appliquer</v-btn>
          </div>
        </v-flex>
      </v-layout> -->

      <!-- CART'S TOTAL -->

      <v-layout row wrap class="total-cart">
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4>
          <h6 class="section-title">Total du Panier</h6>
          <hr class="hz">

          <div class="total pt10">
            <div class="line">
              <strong>Total des articles : </strong> <span>{{AllInCart.length}}</span>
            </div>
            <div class="line">
              <Price class="num" :value="totalAmountInCart" ></Price>
               <!-- <span>{{totalAmountInCart}} F CFA</span> -->
            </div>
          </div>
        </v-flex>

        <v-flex xs12 sm6 md4 offset-sm3 offset-md4 class="mt10">
          <v-btn block dark
          v-if="AllInCart.length"
          @click="placeOrder()" @click.native="paimentDetails = true" color="brown">commander</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import _ from 'lodash'
import ArticleService from '@/services/ArticleService'
import AddressService from '@/services/AddressService'
import OrderService from '@/services/OrderService'
import ItemService from '@/services/ItemService'
import back from '@/components/includes/Back'
import Loading from '@/components/includes/Loading'
import Price from '@/components/includes/Price'

export default {
  name: 'cart',
  components: {
    back,
    Loading,
    Price,
  },
  data() {
    return {
      products: [],
      loading: false,
      paimentDetails: false,
      addresses: null,
      quantity: null,
    }
  },
  created() {
    setTimeout(this.cartElements(), 30000)
  },
  mounted() {
    this.cartElements()
  },
  methods: {
    async cartElements() {
      this.loading = true
      // eslint-disable-next-line
      for (let i = 0; i < this.AllInCart.length; i++) {
        const a = this.AllInCart[i]
        // eslint-disable-next-line
        const art = await ArticleService.details(a.item.article)
        this.products.push(art.data)
      }
      const addr = await AddressService.list()
      try {
        this.addresses = addr.data
        this.addresses = _.filter(addr.data, { user: this.$store.state.user })
        this.loading = false
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
    download(file) {
      if (process.env.NODE_ENV === 'production') {
        return `https://api.tecnonia.com/api/attachements/product_pictures/download/${file}`
      }
      return `https://api.tecnonia.com/api/attachements/product_pictures/download/${file}`
    },
    price(art) {
      const article = _.find(this.products, { id: art })
      return article.finalprice
    },
    label(art) {
      const article = _.find(this.products, { id: art })
      return article.label
    },
    addtocart(p) {
      this.$store.dispatch('setCartAct', p)
    },
    removeItem(p) {
      this.$store.dispatch('removeFromCartAct', p)
    },
    itemAmount(p, q) {
      return p * q
    },
    async placeOrder() {
      try {
        const orderdata = {
          reference: Date.now(),
          items_amount: this.totalAmountInCart,
          total_amount: this.totalAmountInCart + 2500,
          address: this.address,
        }
        const h = {
          Authorization: `${this.$store.state.token}`,
        }
        const newOrder = await OrderService.add(orderdata, h)
        this.$store.dispatch('setOrderAct', newOrder.data.id)
        const cart = this.$store.state.cart
        cart.forEach((element) => {
          const data = {
            quantity: element.quantity,
            amount: element.quantity * this.price(element.item.article),
            order: newOrder.data.id,
            stock: element.item.id,
          }
          ItemService.add(data, h)
        }, this);
        this.$router.push({ name: 'Adresses' })
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
  },
  computed: {
    AllInCart() {
      return this.$store.state.cart
    },
    totalAmountInCart() {
      const amount = []
      // eslint-disable-next-line
      for (let i = 0; i < this.AllInCart.length; i++) {
        const row = this.AllInCart[i]
        const article = _.find(this.products, { id: row.item.article })
        // eslint-disable-next-line
        const totalPrice = row.quantity * article.finalprice
        amount.push(totalPrice)
      }
      return _.sum(amount)
    },
    totalInCart() {
      return this.$store.getters.cartTotalItems
    },
    // isButtonDisabled() {
    //   if (this.$validator.errors.any()) {
    //     return true
    //   }
    //   return false
    // },
  },
  watch: {
    ItemsAmount(p, q) {
      if (this.AllInCart.length > 0) {
        this.itemAmount(p, q)
      }
    },
  },
}
</script>


