<template>
  <div class="product-detail">
      <!-- :clipped-left="clipped" -->
    <v-toolbar
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <back></back>
      <v-toolbar-title>Détails</v-toolbar-title>
    </v-toolbar>
    <Loading v-if="loading"></Loading>

    <v-container
      fluid
      transition="slide-y-reverse-transition"
      class="pt-3 pr-3 pb-5 pl-3"
    >
      <v-layout row wrap class="pb-4">
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4 v-if="!loading">
          <v-layout row wrap>
            <v-flex xs12 >
              <img :src="download(stock.image)" :alt="article.label" class="totalWidthImg">
            </v-flex>
          </v-layout>
          <v-layout row wrap class="thumbnails">
            <v-flex xs12 class="stock-btn" v-if="stocks.length > 1">
              <v-btn icon
                disabled
                v-if="(range - 1) < 0"
                @click="newStock(range, -1)"
              >
                <v-icon>chevron_left</v-icon>
              </v-btn>
              <v-btn icon
                v-if="(range - 1) >= 0"
                @click="newStock(range, -1)"
              >
                <v-icon>chevron_left</v-icon>
              </v-btn>
              <v-btn icon
                v-if="(range + 1) < stocks.length"
                @click="newStock(range, 1)"
              >
                <v-icon>chevron_right</v-icon>
              </v-btn>
              <v-btn icon
                disabled
                v-if="(range + 1) === stocks.length"
                @click="newStock(range, 1)"
              >
                <v-icon>chevron_right</v-icon>
              </v-btn>
            </v-flex>
            <v-flex xs12>
              <v-btn
                color="green darken-1"
                style="color: white"
                class="mr0 share-btn"
                small
                @click="shareProduct">
                <v-icon>share</v-icon> &nbsp; &nbsp; <span>Demander cet article en cadeau</span>
              </v-btn>
            </v-flex>
          </v-layout>

          <hr class="divide mb20">

          <v-layout row wrap class="infos">
            <v-flex xs12>
              <div>
                <Price :value="article.finalprice" ></Price> 
                <!-- <span class="num"> {{article.finalprice}}  f cfa</span> -->
              </div>
              <div class="stock inline">
                <span class="num">{{stock.quantity}}</span>&nbsp;<span class="label">en stock </span>
              </div>
            </v-flex>
          </v-layout>

          <hr class="divide mt20 mb10">

          <v-layout row wrap class="details">
            <v-flex xs12 class="label block">
              <h5> {{article.label}} </h5>
            </v-flex>

            <v-flex xs12 class="block">
              <p class="pt10">
                {{article.description}}
              </p>
            </v-flex>
          </v-layout>

          <hr class="divide mb10">

          <!-- <v-layout row wrap class="features">
            <v-flex xs12>
              <h6>Caractéristiques</h6>
            </v-flex>
            <v-flex xs12>
              <table>
                <tr>
                  <td>Alfreds Futterkiste</td>
                  <td>Maria Anders</td>
                </tr>
                <tr>
                  <td>Centro comercial Moctezuma</td>
                  <td>Francisco Chang</td>
                </tr>
                <tr>
                  <td>Ernst Handel</td>
                  <td>Roland Mendel</td>
                </tr>
                <tr>
                  <td>Island Trading</td>
                  <td>Helen Bennett</td>
                </tr>
                <tr>
                  <td>Laughing Bacchus Winecellars</td>
                  <td>Yoshi Tannamuri</td>
                </tr>
              </table>
            </v-flex>
          </v-layout> -->
        </v-flex>
      </v-layout>

      <!-- ADD TO CART DIALOG BOX -->
      <!-- ====================== -->

      <v-dialog
        v-model="addToCart"
        persistent
        max-width="250px"
        class="cart-dialog"
      >

        <!-- FLOATING ACTION BUTTON -->

        <v-btn dark
          slot="activator"
          class="add-to-cart rounded"
        >
          <v-icon>shopping_cart</v-icon> &nbsp; Ajouter au panier
        </v-btn>

        <!-- ADD TO CART FORM -->

        <v-card>
          <v-card-title>
            <h6 class="dialog-title">Précisez la quantité à ajouter</h6>
          </v-card-title>
          <v-card-text>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-text-field
                label="Précisez la quantité"
                v-model="quantity"
                :rules="quantityRules"
                type="Number"
                required
              ></v-text-field>
            </v-form>
            <!-- <input type="number" class="quantity" v-model="quantity"> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="red darken-1"
              flat
              @click.native="addToCart = false">Annuler</v-btn>
            <v-btn
              color="green darken-1"
              flat
              :disabled="!valid"
              @click="addtocart({item: stock, quantity: quantity})"
              @click.native="addToCart = false"
              @click.stop="goToCart = true"
            >
              Ajouter
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!-- GO TO CART PAGE DIALOG BOX -->
      <!-- ========================== -->

      <v-dialog v-model="goToCart" persistent max-width="250">
        <v-card>
          <v-card-title>
            <h6 class="dialog-title">Le produit a été ajouté avec succès</h6>
          </v-card-title>
          <v-card-text>
            Vous pouvez aller au panier ou rester sur la page courante
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat @click.native="goToCart = false">Rester</v-btn>
            <router-link :to="{ name: 'Panier'}">
              <v-btn color="blue darken-1" flat @click.native="goToCart = false">Panier</v-btn>
            </router-link>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-container>
  </div>
</template>

<script>
import _ from 'lodash'
import ArticleService from '@/services/ArticleService'
import CaracteristicsService from '@/services/CaracteristicsService'
import StockService from '@/services/StockService'
import back from '@/components/includes/Back'
import Loading from '@/components/includes/Loading'
import Price from '@/components/includes/Price'

export default {
  components: {
    back,
    Loading,
    Price,
  },
  data() {
    return {
      addToCart: false,
      goToCart: false,
      showQuantityForm: false,
      article: {},
      stocks: [],
      stock: {},
      caracters: [],
      quantity: 1,
      range: 0,
      showCart: false,
      loading: false,
      valid: true,
      branchUniversalObj: null,
      quantityRules: [
        // eslint-disable-next-line
        v => v > 0||'Vous ne pouvez pas ajoutez cette quantité au panier ',
      ],
    };
  },
  created() {
    this.articleData()
  },
  async mounted() {
    this.articleData()
  },
  methods: {
    async articleData() {
      this.loading = true
      const article = await ArticleService.details(this.$route.params.id)
      const allstock = await StockService.list()
      const instock = _.filter(allstock.data, { article: this.$route.params.id })
      const allcaracters = await CaracteristicsService.list()
      try {
        this.article = article.data
        this.stocks = instock
        this.stock = this.stocks[0]
        this.caracters = allcaracters.data
        this.loading = false
      } catch (err) {
        // eslint-disable-next-line
        console.log('Erreur', err)
      }
    },
    download(file) {
      if (process.env.NODE_ENV === 'production') {
        return `https://api.tecnonia.com/api/attachements/product_pictures/download/${file}`
      }
      return `https://api.tecnonia.com/api/attachements/product_pictures/download/${file}`
      // return `http://localhost:3000/api/attachements/img/download/${file}`
    },
    addtocart(p) {
      this.$store.dispatch('setCartAct', p)
    },
    cleancart(p) {
      this.$store.dispatch('cleanCartAct', p)
    },
    newStock(range, other) {
      // eslint-disable-next-line
      range = this.range
      let index = 0
      if (other !== 0) {
        index = range + other
      } else {
        index = range
      }
      this.range = index
      this.stock = this.stocks[index]
      return this.stock
    },
    stockCaracters(stck) {
      const caracters = _.filter(this.caracters, { stock: stck })
      return caracters
    },
    shareProduct() {
      if (typeof Branch === 'undefined') {
        // eslint-disable-next-line
        console.log('Can\'t find branch');
      } else {
        // optional fields
        const analytics = {}

        // optional fields
        const properties = {
          canonicalIdentifier: this.$route.params.id,
          title: this.article.label,
          contentDescription: this.article.description,
          productDetail: this.$route.params.id,
          contentImageUrl: `https://api.tecnonia.com/api/attachements/product_pictures/download/${this.stock.image}`,
          $deeplink_path: this.$route.params.id,
        }


        const message = this.article.description
        /* eslint-disable no-undef */
        Branch.createBranchUniversalObject(properties).then((res) => {
          // eslint-disable-next-line
          console.log('Response: ' + JSON.stringify(res))
          this.branchUniversalObj = res
        }).catch((err) => {
          // eslint-disable-next-line
          console.log('Error: ' + JSON.stringify(err))
        })

        // this.branchUniversalObj.generateShortUrl(analytics, properties).then((res) => {
        // // eslint-disable-next-line
        //   console.log('create link Response: ' + JSON.stringify(res.url))
        // }).catch((err) => {
        //   // eslint-disable-next-line
        //   console.log('create link Error: ' + JSON.stringify(err))
        // })
        // optional listeners (must be called before showShareSheet)
        this.branchUniversalObj.onShareSheetLaunched((launchRes) => {
          // android only
          // eslint-disable-next-line
          console.log('onShareSheetLaunch: ' +launchRes)
        })
        this.branchUniversalObj.onShareSheetDismissed((dismissRes) => {
          // eslint-disable-next-line
          console.log('onShareSheetDismissed: ' +dismissRes)
        })
        this.branchUniversalObj.onLinkShareResponse((shareRes) => {
          // eslint-disable-next-line
          console.log('onLinkShareResponse: ' +shareRes)
        })
        this.branchUniversalObj.onChannelSelected((selecteRes) => {
          // android only
          // eslint-disable-next-line
          console.log('onChannelSelected: ' +selecteRes)
        })

        // share sheet
        this.branchUniversalObj.showShareSheet(analytics, properties, message)
      }
    },
  },
  computed: {
    totalInCart() {
      return this.$store.getters.cartTotalItems
    },
    // isButtonDisabled() {
    //   if (this.$validator.errors.any()) {
    //     return true
    //   }
    //   return false
    // },
  },
}
</script>

<style lang="sass">
  .dialog-title
    font-size: 1.2rem
    font-family: robotoBold!important
    color: #555

  .quantity
    height: 35px
    line-height: 35px
    border: 1px solid #ccc
    border-radius: 3px
    padding-left: 5px
    width: 100%
    outline: none
</style>



