import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Items')
  },
  details(parameter) {
    return Api().get(`Items/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Items', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Items/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter, headers) {
    return Api().delete(`Items/${parameter}`, {
      headers,
    })
  },
}



