<template>
  <v-app>
    <v-content>
      <router-view/>
    </v-content>
  </v-app>
</template>

<script>
import Vue from 'vue';

const FCM = require('fcm-push');
// const FCM = require('fcm-node');

export default {
  data() {
    return {
      cordova: Vue.cordova,
      clipped: false,
      drawer: false,
      title: 'Oqenyite',
      userToken: '',
      appVersion: '',
      forceUpdate: '',
      currentVersion: '',
      updateUrl: '',
    };
  },
  name: 'App',
  created() {
    this.cordova.on('deviceready', () => {
      this.onDeviceReady();
    });
  },
  methods: {
    handleBranch() {
      if (typeof Branch === 'undefined') {
        // eslint-disable-next-line
        console.log('Can\'t find branch');
      } else {
        /* eslint-disable no-undef */
        Branch.initSession((data) => {
          if (data['+clicked_branch_link']) {
            // read deep link data on click
            // eslint-disable-next-line
            console.log('Deep Link Data: ' + JSON.stringify(data));
            // eslint-disable-next-line
            this.$router.push({ name: 'Detail', params: { id: data['$deeplink_path'] } })
          }
        });
        Branch.setCookieBasedMatching('oqenyite.app.link');
      }
    },
    onDeviceReady() {
      // Handle the device ready event.
      // eslint-disable-next-line
      // alert(this.initiALL);
      this.handleBranch()
      this.gfToken()
      this.remoteConfig()
      // this.getMessageFromFCM()
      // this.pushNotif()
      this.cordova.on('pause', this.onPause, false);
      this.cordova.on('resume', this.onResume, false);
      // if (this.cordova.device.platform === 'Android') {
      //   document.addEventListener('backbutton', this.onBackKeyDown, false);
      // }
    },
    onPause() {
      // Handle the pause lifecycle event.
      // eslint-disable-next-line
      // console.log('pause');
    },
    onResume() {
      // Handle the resume lifecycle event.
      // SetTimeout required for iOS.
      setTimeout(() => {
        // eslint-disable-next-line
        // console.log('resume');
      }, 0);
    },
    onBackKeyDown() {
      // Handle the back-button event on Android. By default it will exit the app.
      // navigator.app.exitApp();
    },
    gfToken() {
      window.FirebasePlugin.getToken((token) => {
        // save this server-side and use it to push notifications to this device
        this.userToken = token
        // eslint-disable-next-line
        // alert(this.userToken);
      }, (error) => {
        // eslint-disable-next-line
        alert(error);
      });

      window.FirebasePlugin.onTokenRefresh((token) => {
        const storage = window.localStorage
        storage.setItem('user_token', token)
        // save this server-side and use it to push notifications to this device
        this.userToken = token
        this.pushNotif()
      }, (error) => {
        // eslint-disable-next-line
        alert(error);
      });
    },
    remoteConfig() {
      window.FirebasePlugin.fetch(60, () => {
        window.FirebasePlugin.activateFetched((activated) => {
          // eslint-disable-next-line
          console.log(activated)
          const settings = {
            developerModeEnabled: true,
          }
          window.FirebasePlugin.setConfigSettings(settings)
          window.FirebasePlugin.getValue('force_update_required', (value) => {
            this.forceUpdate = value
            // eslint-disable-next-line
            // alert(this.forceUpdate)
            if (this.forceUpdate === 'true') {
              window.FirebasePlugin.getValue('force_update_current_version', (vers) => {
                this.currentVersion = vers
                this.appVersion = AppVersion.version
                if (this.appVersion !== this.currentVersion) {
                  window.FirebasePlugin.getValue('force_update_store_url', (url) => {
                    this.updateUrl = url
                    const message = 'Une nouvelle version de Oqenyite est disponible'
                    const title = 'Mise à jour'
                    const buttonLabels = ['mettre à jour', 'Pas maintenant']
                    const confirmCallback = (buttonIndex) => {
                      if (buttonIndex === 1) {
                        window.open('https://play.google.com/store/apps/details?id=tecnonia.com.oqenyite&hl=fr')
                      } else {
                        navigator.app.exitApp()
                      }
                    };
                    navigator.notification.confirm(message, confirmCallback, title, buttonLabels)
                  }, (error) => {
                    // eslint-disable-next-line
                    alert('error: '+error);
                  });
                }
              }, (error) => {
                // eslint-disable-next-line
                alert('error: '+error);
              });
            }
          }, (error) => {
            // eslint-disable-next-line
            alert('error: '+error);
          });
        }, (error) => {
          // eslint-disable-next-line
          console.error(error);
        });
      });
    },
    getMessageFromFCM() {
      // alert("onNotificationOpen");
      try {
        window.FirebasePlugin.onNotificationOpen((data) => {
          // eslint-disable-next-line
          // alert(JSON.stringify(data));
          if (data.tap === 'true') {
            if (data.redirect === 'true') {
              if (data.toCart === 'true') {
                this.$router.push({ name: 'Panier' })
              }
            }
          }
        });
      } catch (e) {
        // eslint-disable-next-line
        alert(e);
      }
    },
    pushNotif() {
      const deviceID = this.userToken;
      // eslint-disable-next-line
      const apiKey = 'AAAAYOqV0sw:APA91bFpZjx6U7g4x60renFoI6sJ-RcIu83JI5PUgGXwAXC6Va9goZxT87ZP95rCgHTkyTGu0gxnnzHw2gzuBE4g0IIHM6ApOuHyNWgQDBKnDWah5tRMiC5r7cT4XiPxx4uP86SLZcGA';
      const fcm = new FCM(apiKey);
      const message = {
        to: deviceID,
        collapse_key: 'tecnonia.com.oqenyite',
        notification: {
          title: 'Oqenyite notification',
          body: 'Loaded from URL',
          color: '#d00000',
        },
      };

      fcm.send(message, (err, response) => {
        if (err) {
          // eslint-disable-next-line
          alert(err);
          // eslint-disable-next-line
          console.log("Something has gone wrong!");
        } else {
          // eslint-disable-next-line
          console.log("Successfully sent with response: "+ response);
        }
      });
    },
  },
};
</script>

<style lang="sass" src="../static/sass/main.sass"></style>



