import Vue from 'vue';
import Router from 'vue-router';
import store from '@/store/store'

import Home from '@/components/Home';
import Detail from '@/components/Detail';
import Panier from '@/components/Panier';
import Adresses from '@/components/Adresses';
import AdresseNew from '@/components/AdresseNew';
import Payment from '@/components/Payment';
import PaymentProcess from '@/components/PaymentProcess';
import Login from '@/components/Login';
import Signup from '@/components/Signup';
import Search from '@/components/Search';
import Commande from '@/components/Commande';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detail,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/panier',
      name: 'Panier',
      component: Panier,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/adresses',
      name: 'Adresses',
      component: Adresses,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/paiement',
      name: 'Payment',
      component: Payment,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/payment-processing',
      name: 'PaymentProcess',
      component: PaymentProcess,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/new-address',
      name: 'AdresseNew',
      component: AdresseNew,
      meta: {
        requiresAuth: true,
      },
    },

    {
      path: '/connexion',
      name: 'Login',
      component: Login,
    },

    {
      path: '/inscription',
      name: 'Signup',
      component: Signup,
    },

    {
      path: '/recherche',
      name: 'Search',
      component: Search,
    },

    {
      path: '/commande',
      name: 'Commande',
      component: Commande,
    },
  ],
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!store.state.isUserLoggedIn) {
      next({
        path: '/inscription',
        query: {
          redirect: to.fullPath,
        },
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router




