<template>
  <div>
      <!-- :clipped-left="clipped" -->
    <v-toolbar
      dark
      app
      class="oq-toolbar no-shadow"
      transition="slide-y-reverse-transition"
    >
      <v-toolbar-title>Connexion</v-toolbar-title>
    </v-toolbar>

    <v-container
      fluid
      transition="slide-y-reverse-transition"
      class="connexion"
    >
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-text-field
              label="Votre email"
              v-model="email"
              :rules="emailRules"
              required
            ></v-text-field>
            <v-text-field
              type="Password"
              label="Votre mot de passe"
              v-model="password"
              :rules="passwordRules"
              required
              @keyup.enter="login()"
            ></v-text-field>
          </v-form>
          <v-btn
            color="blue darken-1"
            style="color: white"
            :disabled="!valid"
            @click="login()"
            class="val-btn"
          >
            Se connecter
          </v-btn>
        </v-flex>
      </v-layout>

      <v-layout row wrap class="inscription-text">
        <v-flex xs12 sm6 md4 offset-sm3 offset-md4 class="text-xs-center">
          <div class="question"><span>Vous n'avez pas de compte ?</span></div>
          <div class="lien"><router-link :to="{ name: 'Signup'}" >Créez un compte</router-link></div>
        </v-flex>
      </v-layout>
    </v-container> 
  </div>
</template>
<script>
import _ from 'lodash'
import LoginService from '@/services/LoginService'
import AddressService from '@/services/AddressService'

export default {
  name: 'login',
  data() {
    return {
      email: '',
      password: '',
      loading: false,
      valid: true,
      emailRules: [
        // eslint-disable-next-line
        v => !!v||'Ce champ est obligatoire',
        // eslint-disable-next-line
        v => /\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3}(?:\s+)?)+$/.test(v)||'L\'e-mail doit être valide',
      ],
      passwordRules: [
        // eslint-disable-next-line
        v => !!v||'Le mot de passe est obligatoire',
      ],
    }
  },
  // created() {
  //   this.login()
  // },
  methods: {
    async login() {
      const e = this.email.trim()
      const logindata = {
        email: e,
        password: this.password,
      }
      const response = await LoginService.login(logindata)
      try {
        const t = response.data.id
        const u = response.data.userId
        this.$store.dispatch('setUserAct', u)
        this.$store.dispatch('setTokenAct', t)
        // this.email = ''
        // this.password = ''
        this.redirect()
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
    isLoggedIn() {
      return this.$store.state.isUserLoggedIn
    },
    async redirect() {
      const addresses = await AddressService.list()
      try {
        const useraddresses = _.filter(addresses.data, { user: this.$store.state.user })
        if (useraddresses.length > 0) {
          this.$router.push({ name: 'Home' })
        } else {
          this.$router.push({ name: 'AdresseNew' })
        }
      } catch (error) {
        // eslint-disable-next-line
        console.log(error)
      }
    },
  },
}
</script>

<style lang="sass">
  .input
    height: 35px
    line-height: 35px
    border-radius: 3px
    outline-color: #e8e8e8
    padding-left: 7px
    width: 80%
    margin-top: 7px
    margin-bottom: 60px
    // color: #333
    font-family: robotoLight
    // border: 1px solid #aaa
</style>



