import Api from '@/services/Api'

export default {
  list() {
    return Api().get('Orders')
  },
  details(parameter) {
    return Api().get(`Orders/${parameter}`)
  },
  add(data, headers) {
    return Api().post('Orders', data, {
      headers,
    })
  },
  update(parameter, data, headers) {
    return Api().put(`Orders/${parameter}`, data, {
      headers,
    })
  },
  delete(parameter, headers) {
    return Api().delete(`Orders/${parameter}`, {
      headers,
    })
  },
}



